@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top-links {
  display: none;
}

@media (min-width: 768px) {
  .leftnav {
    flex-grow: 1;
  }

  .rightnav {
    padding-right: 10px;
  }

  .top-links {
    display: flex;
    justify-content: flex-end;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    background-color: black;
    color: white;
    padding: 10px;
  }
}
.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  background-origin: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button-line {
  width: 30px;
  height: 2px;
  background: black;
}
.main-links {
  display: none;
}

.navbar-toggle-button {
  display: block;
}

@media (min-width: 768px) {
  .large-logo {
    max-width: 150px;
    float: left;
  }

  .main-links {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: space-around;
    align-items: center;
    border-bottom: solid 2px #d0d0d0;
    padding: 0px 10px 10px 0px;
  }

  .main-nav {
    display: inline;
  }

  .navbar-toggle-button {
    display: none;
  }

  .solutions-link {
    text-decoration: none;
    color: black;
  }

  #search {
    padding: 10px;
    border: solid 1px #d0d0d0;
    border-radius: 5px;
  }
}
.drop-down {
  display: none;
}

.drop-down.open {
  display: block;
  margin-top: 40px;
}

.solutions__links {
  padding: 20px;
}

li {
  list-style-type: none;
}
.mobile-nav {
  text-decoration: none;
  color: black;
}

.mobile-nav__link {
  padding: 15px;
  border-bottom: solid 1px #d0d0d0;
}
.mobile-nav-link {
  text-decoration: none;
  color: black;
}

.mobile-solutions__link {
  padding: 15px;
  border-bottom: solid 1px #d0d0d0;
}
* {
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans', sans-serif;
}



